import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import { FormControl, FormGroup, Validators, NgForm } from '@angular/forms';
import { SnotifyService, SnotifyPosition, SnotifyToastConfig } from 'ng-snotify';

import { LuckyDrawService } from '../../shared/data/luckyDraw.service';
import { DatatableComponent } from '@swimlane/ngx-datatable';

import * as jsPDF from 'jspdf';
import html2canvas from 'html2canvas';

@Component({
  selector: 'app-lucky-draw',
  templateUrl: './lucky-draw.component.html',
  styleUrls: ['./lucky-draw.component.scss']
})
export class LuckyDrawComponent implements OnInit {

  @ViewChild(DatatableComponent) table: DatatableComponent;
  dataSource = [];
  collectionSize:any;
  columns = [
    { name: 'Name'},
    { name: 'Member Number'},
    { name: 'Draw Number'},
    { name: 'Created'}
  ];
  drawId:any;
  memberNumber:any;
  memberName:any;
  drawId1:any;
  memberNumber1:any;
  memberName1:any;
  newDraw = false;
  time1: any;
  elementType: 'url' | 'canvas' | 'img' = 'url';
  downloadDiv = false;
  constructor(public luckyDrawService: LuckyDrawService) { }

  ngOnInit() {
    this.getLuckyDraw();
  }

  getLuckyDraw(){
    this.luckyDrawService.getDetails().then(data => {
      console.log(data['details']);
      if(data['status'] == 'success'){
        this.dataSource = data['details'];
        this.collectionSize = data['details'].length;
      }
      else{
        this.dataSource = [];
        this.collectionSize = 0;
      }
    },
    error => {
    });
  }

  generateDraw(){
    this.luckyDrawService.generateLuckyDraw().then(data => {
      console.log(data['details']);
      if(data['status'] == 'success'){
        this.newDraw = true;
        this.drawId = data['details'].luckyDrawnumber;
        this.memberNumber = data['details'].membernumber;
        this.memberName = data['details'].name;
        this.luckyDrawService.insertLuckyDraw(data['details']).then(data1 => {
          console.log(data1);
          this.getLuckyDraw();
        });
      }
      else{
        this.newDraw = false;
        this.drawId = '';
        this.memberNumber = '';
        this.memberName = '';
      }
    },
    error => {
    });
  }

  downloadPrepare(rowDetail){
    debugger;
    this.downloadDiv = true;
    this.drawId1 = rowDetail.drawNumber;
    this.memberNumber1 = rowDetail.memberNumber;
    this.memberName1 = rowDetail.name;
    this.time1 = rowDetail.created;

    // let string = '<div>'+
    //   '<div class="card">'+
    //     '<div class="card-header text-center">'+
    //       '<img src="../../../assets/img/logos/logo.png" alt="" style="width: 40%;">'+
    //     '</div>'+
    //     '<div class="card-body" style="padding: 0 0 10px 10px;">'+
    //       '<h3 class="text-center">'+rowDetail.drawNumber+'</h3>'+
    //       '<span class="float-right">'+rowDetail.created+'</span>'+
    //       '<b>Name</b>: '+rowDetail.name+'<br />'+
    //       '<b>Member Number</b>: '+rowDetail.memberNumber
    //     '</div>'+
    //   '</div>'+
    // '</div>';
  }

  download(){
    html2canvas(document.querySelector("#parentdiv")).then(canvas => {
      var imgWidth = 100;
      var pageHeight = 150;
      var imgHeight = canvas.height * imgWidth / canvas.width;
      var heightLeft = imgHeight;
      const contentDataURL = canvas.toDataURL('image/png')
      let pdf = new jsPDF('p', 'mm', 'a4'); // A4 size page of PDF
      var position = 0;
      pdf.addImage(contentDataURL, 'PNG', 0, position, imgWidth, imgHeight);
      this.downloadDiv = false;
      pdf.save(this.drawId1+'_'+this.memberNumber1+'.pdf');
      this.drawId1 = '';
      this.memberNumber1 = '';
      this.memberName1 = '';
      this.time1 = '';
    });
  }

}
